import styled from 'styled-components'
export const MineWrapper = styled.div`
  .mine {
    box-sizing: border-box;
    height: 100vh;
    padding: 20px;
    background-color: rgb(66, 117, 164);
    .top {
      .notlogin {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100vh;
      }
      display: flex;
      justify-content: space-around;
      align-items: center;
      .login {
        width: 80px;
        height: 24px;
        background-color: red;
        color: white;
        line-height: 24px;
        text-align: center;
        border-radius: 10px;
      }
      .topright {
        width: 100px;
        height: 24px;
        line-height: 24px;
        text-align: center;
        border: 1px solid red;
        border-radius: 12px;
      }
    }
    .center {
      margin: 10px 0;
      .centerMain {
        margin: 18px 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .centerLeft {
          display: flex;
          justify-content: space-between;
          align-items: center;
          width: 150px;
        }
        .centerRight {
          width: 100px;
          height: 24px;
          line-height: 24px;
          text-align: center;
          border: 1px solid grey;
          border-radius: 12px;
        }
      }
    }
    .end {
      padding-top: 10px;
      .card {
        margin: -20px;
        height: 100vh;
        overflow: auto;
      }
    }
  }
`
