import { useEffect, memo, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import ImgCrop from 'antd-img-crop'
import { fetchuserdata } from '../../redux/modules/user'
import istokenexpire from '../../components/istokenexpire'
import { MineWrapper } from './style'
import { Avatar, Card } from 'antd-mobile'
import TabsContent02 from '../home/tabs-content02'
import TabsContent03 from './tabs-content01'
import { Drawer, Form, Input, Space, Button, Select, Upload } from 'antd'
import { AddOutline } from 'antd-mobile-icons'
import http from '../../utils/http'

import Tabss from '../home/tabs'
import TabsContent from './tabs-content'
import { removeToken } from '../../utils/storage'
import getUserFromToken from '../../components/takeuserfromtoken'

const { Option } = Select
const tabItems = [
  { key: '0', title: '赞过' },
  { key: '1', title: '笔记' },
  { key: '2', title: '收藏' },
]

function Mine({ navigate }) {
  const res1 = useSelector((state) => state.user.userdata.data) || []
  const res = res1[0]
  const res2 = res1[1]
  const dispatch = useDispatch()
  useEffect(() => {
    if (!istokenexpire()) {
      dispatch(fetchuserdata())
    }
  }, [dispatch])
  const [forceUpdateFlag, setForceUpdateFlag] = useState(false)

  //抽屉组件相关
  const [open, setOpen] = useState(false)
  const showDrawer = () => {
    setOpen(true)
  }
  const onClose = () => {
    setOpen(false)
  }
  const [fileList, setFileList] = useState([])
  const [ImageUrl, setImageUrl] = useState()
  useEffect(() => {
    setImageUrl(res?.ava_url)
  }, [res?.ava_url])
  const getBase64 = (img, callback) => {
    const reader = new FileReader()
    reader.addEventListener('load', () => callback(reader.result))
    reader.readAsDataURL(img)
  }

  const props = {
    onRemove: (file) => {
      const index = fileList.indexOf(file)
      const newFileList = fileList.slice()
      newFileList.splice(index, 1)
      setFileList(newFileList)
    },
    beforeUpload: (file) => {
      getBase64(file, (url) => {
        setImageUrl(url)
      })
      setFileList([...fileList, file])
      return false
    },
    fileList,
  }
  const onFinish = async ({ surname, gender, avatar, intro }) => {
    // 创建一个 FormData 对象
    const updateUserInfo = new FormData()
    //将form表单内容添加进去
    updateUserInfo.append('surname', surname)
    updateUserInfo.append('gender', gender)
    updateUserInfo.append('intro', intro)
    // 将上传所有文件添加到 FormData 中
    fileList.forEach((file) => {
      updateUserInfo.append('avatar', file)
    })
    const res = await http.patch(
      `/user/my/${getUserFromToken()}`,
      updateUserInfo
    )
    onClose()
    window.location.reload()
  }

  return (
    <MineWrapper>
      <div className="mine">
        <div className="top">
          {istokenexpire() ? (
            <div className="notlogin">
              <h2>欢迎来到小红书</h2>
              <div onClick={() => navigate('/login')} className="login">
                立即登陆
              </div>
            </div>
          ) : (
            <div
              style={{
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center',
                width: 390,
              }}
            >
              <div
                style={{
                  display: 'flex',
                  alignItems: 'center',
                }}
              >
                <Avatar
                  src={res?.ava_url}
                  style={{
                    width: '64px',
                    height: '64px',
                    borderRadius: '32px',
                    marginRight: '10px',
                  }}
                />
                <span>{res?.surname || res?.name}</span>
              </div>
              <div
                className="topright"
                onClick={() => {
                  removeToken()
                  setForceUpdateFlag((prevFlag) => !prevFlag)
                }}
              >
                退出登陆
              </div>
            </div>
          )}
        </div>
        {!istokenexpire() ? (
          <>
            <div className="center">
              <div>{res?.intro || '请编辑资料做个自我介绍吧~'}</div>
              <div style={{ marginTop: '30px' }}>
                {res?.gender === '1'
                  ? '男'
                  : res?.gender === '0'
                  ? '女'
                  : '保密'}
              </div>
              <div className="centerMain">
                <div className="centerLeft">
                  <div style={{ textAlign: 'center', width: '50px' }}>
                    <div style={{ marginBottom: '5px' }}>
                      {res2?.beFollowCounts}
                    </div>
                    <div>获关注</div>
                  </div>
                  <div style={{ textAlign: 'center', width: '100px' }}>
                    <div style={{ marginBottom: '5px' }}>
                      {res2?.beFavorAndCollectCounts}
                    </div>
                    <div>获赞与收藏</div>
                  </div>
                </div>
                <div className="centerRight" onClick={showDrawer}>
                  编辑资料
                </div>
              </div>
            </div>
            <div className="end">
              <Card className="card" style={{ padding: '0' }}>
                <Tabss
                  tabitems={tabItems}
                  tabscontent={TabsContent}
                  tabscontent02={TabsContent02}
                  tabscontent03={TabsContent03}
                />
              </Card>
            </div>
          </>
        ) : null}

        <Drawer
          title="编辑资料"
          placement="bottom"
          onClose={onClose}
          height="610px"
          open={open}
          styles={{
            body: {
              paddingBottom: 80,
            },
          }}
        >
          <Form
            style={{ overflowX: 'hidden' }}
            onFinish={onFinish}
            labelCol={{
              span: 8,
            }}
            wrapperCol={{
              span: 16,
            }}
            initialValues={{
              name: res?.name,
              surname: res?.surname,
              gender: res?.gender,
              intro: res?.intro,
            }}
          >
            <Form.Item label="用户名" name="name">
              <Input readOnly />
            </Form.Item>
            <Form.Item name="surname" label="昵称">
              <Input placeholder="请输入你的昵称~" />
            </Form.Item>

            <Form.Item name="avatar" label="头像">
              <div>
                <ImgCrop rotationSlider modalWidth={350} modalHeight={350}>
                  <Upload
                    listType="picture-card"
                    showUploadList={false}
                    {...props}
                  >
                    {!!ImageUrl ? (
                      <img
                        className="head"
                        src={ImageUrl}
                        style={{ width: 100, height: 100 }}
                        alt="cover"
                      />
                    ) : (
                      <div>
                        <AddOutline fontSize={24} color="grey" />
                      </div>
                    )}
                  </Upload>
                </ImgCrop>
              </div>
            </Form.Item>

            <Form.Item name="gender" label="性别">
              <Select placeholder="选择性别">
                <Option value="1">男</Option>
                <Option value="0">女</Option>
              </Select>
            </Form.Item>

            <Form.Item name="intro" label="个人简介">
              <Input.TextArea rows={4} placeholder="说点什么吧~" />
            </Form.Item>
            <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
              <Space>
                <Button onClick={onClose}>取消</Button>
                <Button type="primary" htmlType="submit">
                  提交
                </Button>
              </Space>
            </Form.Item>
          </Form>
        </Drawer>
      </div>
    </MineWrapper>
  )
}
export default memo(Mine)
