import { memo, useEffect, useState } from 'react'
import { TabMineWrapper } from './style'
import { useDispatch, useSelector } from 'react-redux'
import Item from '../../home/tabs-content/item'
import { Drawer } from 'antd'
import Detail from '../../home/detail/detail'
import { fetchdata04 } from '../../../redux/modules/home'
import isTokenExpired from '../../../components/istokenexpire'
import getUserFromToken from '../../../components/takeuserfromtoken'

function MineTabsContent() {
  const res = useSelector((state) => state.home.listNotes)
  const [columns, setColumns] = useState([[], []])
  useEffect(() => {
    const newColumns = res?.reduce((result, item, index) => {
      const columnIndex = index % 2
      if (!result[columnIndex]) {
        result[columnIndex] = []
      }
      result[columnIndex].push(item)
      return result
    }, [])

    setColumns(newColumns)
  }, [res])
  const dispatch = useDispatch()
  useEffect(() => {
    if (!isTokenExpired() && !!getUserFromToken()) {
      dispatch(fetchdata04())
    }
  }, [])
  //抽屉组件相关
  const [open, setOpen] = useState(false)
  const showDrawer = () => {
    setOpen(true)
  }
  const onClose = () => {
    setOpen(false)
  }
  //detail页面的id
  const [detailId, setDetailId] = useState()
  return (
    <TabMineWrapper>
      <div className="tab02">
        {res?.length === 0 && (
          <div style={{ marginTop: '30px', marginLeft: '30px', color: 'gray' }}>
            这里是一片荒草地，请开始发布你的第一篇笔记吧~
          </div>
        )}
        <div className="content">
          <div className="left">
            {columns[0]?.map((item) => {
              return (
                <div key={item.id}>
                  <Item
                    id={item.id}
                    src={item.cover}
                    title={item.title}
                    favor={item.favorCount}
                    username={item.user.name}
                    surname={item.user.surname}
                    useravatar={item.user.avatar}
                    favorUsers={item.favorUserIds}
                    detailId={detailId}
                    setDetailId={setDetailId}
                    showDrawer={showDrawer}
                  />
                </div>
              )
            })}
          </div>
          <div className="right">
            {columns[1]?.map((item) => {
              return (
                <div key={item.id}>
                  <Item
                    id={item.id}
                    src={item.cover}
                    title={item.title}
                    favor={item.favorCount}
                    username={item.user.name}
                    surname={item.user.surname}
                    useravatar={item.user.avatar}
                    favorUsers={item.favorUserIds}
                    detailId={detailId}
                    setDetailId={setDetailId}
                    showDrawer={showDrawer}
                  />
                </div>
              )
            })}
          </div>
        </div>
        <Drawer
          placement="right"
          onClose={onClose}
          closeIcon={false}
          width="100%"
          open={open}
          styles={{ body: { padding: '0px' } }}
        >
          <Detail id={detailId} onClose={onClose} show={true} />
        </Drawer>
        {(isTokenExpired() || !getUserFromToken()) && (
          <div style={{ margin: '20px' }}>登陆后才能看到你发布的笔记~</div>
        )}
      </div>
    </TabMineWrapper>
  )
}
export default memo(MineTabsContent)
